<template>
	<div id="advertise-stick">
		<public-header title="商家推广"></public-header>
		<div class="content">
			<div class="content-inner">
				<div class="padding-15-lr padding-10-tb c-gray font-14">选择广告位置</div>
				<van-radio-group v-model="type">
					<van-cell-group>
						<van-cell clickable @click="onSelectPosition(-1, 'recommendHome')">
							<div slot="title">为您优选首页 </div>
							<van-radio name="recommendHome" />
						</van-cell>
						<van-cell clickable @click="onSelectPosition(-1, 'recommendOther')">
							<div slot="title">为您优选更多页 </div>
							<van-radio name="recommendOther" />
						</van-cell>
					</van-cell-group>
				</van-radio-group>
				<van-radio-group v-model="day[type]" class="margin-10-t" >
					<van-cell-group>
						<van-cell>
							<div slot="title">
								广告位总数{{advertise[type].total}}个 {{advertise[type].leave > 0 ? '剩余' + advertise[type].leave + '个位置' : '（已售罄）'}} 价目:
							</div>
						</van-cell>
						<van-cell :title="'购买广告' + feeItem.day + '天' + feeItem.fee + '元'" clickable @click="onSelectPosition(index, 'day')" v-for="(feeItem, index) in advertise[type].prices" :key="index">
							<van-radio :name="index" v-if="advertise[type].leave > 0" />
						</van-cell>
					</van-cell-group>
				</van-radio-group>
				<div class="padding-15-lr padding-10-tb c-gray font-14">选择支付方式</div>
				<van-radio-group v-model="pay_type">
					<van-cell-group>
						<van-cell clickable @click="pay_type = 'credit'" class="no-fix">
							<div slot="title">
								<span class="margin-5-r">余额支付</span>
								<span>账户余额: ¥{{amount}}</span>
							</div>
							<van-radio name="credit" />
						</van-cell>
					</van-cell-group>
				</van-radio-group>
			</div>
			<div class="fix-bottom van-hairline--top">
				<van-button size="normal" block class="bg-info" @click="onSubmit()" v-if="advertise[type].leave">立即购买</van-button>
				<van-button size="normal" block class="van-button--disabled" v-else>此广告位已售罄，去看看其他广告位</van-button>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData, setData} from "@/controller/funcCommon"
	import { Dialog } from 'vant';
	export default {
		components: {
			publicHeader
		},
		data() {
			return {
				showPreLoading: true,
				type: 'recommendHome',
				advertise: {
					recommendHome: {},
					recommendOther: {},
				},
				amount: 0,
				day: {
					recommendHome: 0,
					recommendOther: 0,
				},
				pay_type: 'credit'
			}
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: that,
					url: 'manage/advertise/index/recommend',
					data: {
						type: that.type
					},
					success: (res) => {
						that.advertise = res.advertise;
						that.amount = res.amount;
						if(that.advertise['recommendHome'].leave > 0) {
							for(var day in that.advertise['recommendHome'].prices) {
								that.day['recommendHome'] = day;
								break;
							}
						} else if(that.advertise['recommendOther'].leave > 0) {
							that.type = 'recommendOther';
							for(var day in that.advertise['recommendOther'].prices) {
								that.data.day['recommendOther'] = day;
								break;
							}
						}
					}
				});
			},
			onSelectPosition(index, type) {
				var that = this;
				if(type == 'day') {
					var leave = that.advertise[that.type].leave;
					if(leave > 0) {
						that.day[that.type] = index;
					}
				} else {
					that.type = type;
				}
			},
			onSubmit() {
				var that = this;
				if(!that.type) {
					that.util.$toast('请选择购买位置', '', 1000);
					return false;
				}
				if(that.day[that.type] == 0) {
					that.util.$toast('请选择购买天数', '', 1000);
					return false;
				}
				Dialog.confirm({
					title: '提示',
					message: '确定购买该推广活动吗?',
				}).then(() => {
					setData({
						vue: that,
						url: 'manage/advertise/index/recommend',
						data: {
							type: that.type,
							day: that.day[that.type],
							pay_type: that.pay_type
						},
						success: function(res) {
							that.util.pay({
								pay_type: that.pay_type,
								order_type: 'advertise',
								order_id: res.id,
								sid: res.sid,
								vue: that
							});
						}
					});
				}).catch(() => {

				});
			}
		},
		mounted(){
			this.onLoad();
		}
	}
</script>

<style>
	#advertise-stick .content .content-inner{
		position: absolute;
		top: 0;
		left: 0;
		bottom: 64px;
		right: 0;
		overflow-x: hidden;
		overflow-y: scroll;
	}
	#advertise-stick .fix-bottom{
		position: absolute;
		left: 0;
		right: 0;
		bottom: 0;
		padding: 10px 15px;
		background-color: #fff;
	}
</style>


